page {
  width: 100%;
  height: 100%;
}

.wrap {
  width: 100%;
  height: 100%;
  image {
    width: 100%;
    height: 100%;
  }
}
.f88 {
  font-size: 88rpx;
}
.share-p {
  position: absolute;
  width: 100%;
  top: 45%;
  transform: translateY(-50%);
}
.scroll {
  position: absolute;
  left: 50%;
  bottom: 80rpx;
  transform: translate(-50%, 0);
  width: 364rpx;
  height: 120rpx;
  background-color: #fff;
  background:rgba(255,255,255,0.18);
  border-radius: 8rpx;
}
.share-btn {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}